.signup{
    padding: 150px 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.signup__container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #1c2237;
    width: 400px;
    height: 400px;
    border-radius: 10px;
}
.signup__container h2{
    color: white;
    margin-bottom: 2rem;
}
.form-login form{
    display: flex;
    flex-direction: column;
}
.form-login form input{
    border: 1px solid black;
    margin-bottom: 1.5rem;
}

.form-login form button {
    background-color: #1c2237;
    color: #fff;
}

.form-login form input, .form-login form button{
    border-radius: 5px;
}